import ProfileCard from '../Cards/Profile'
import service1 from '../../static/images/urpe/services1.png'
import service2 from '../../static/images/urpe/services2.png'
import service3 from '../../static/images/urpe/services3.png'
import service4 from '../../static/images/urpe/services4.png'

import logo from '../../static/images/urpe/logo.png'
export default {
  components: {
    ProfileCard
  },
  data() {
    return {
      visible: false,
      index: 0,
      loaded: false,
      item: 0,
      logo,
      service1,
      service2,
      service3,
      service4,
      nroTlf: '+1 800-643-6740',
      // categories: [
      //   'all',
      //   'Orthopedic',
      //   'Nutritionist',
      //   'Pediatric',
      //   'Anaesthestic'
      // ],
      doctorsData: [
        {
          avatar: service1,
          title: 'Tramites de inmigracion ',
          description: 'Nos encargamos de todo tu proceso de Visado y/o Asilo.'
        },
        {
          avatar: service2,
          title: 'Asesora de impuestos',
          description:
            'Simplificando tus Impuestos, maximizando tus devoluciones.'
        },
        {
          avatar: service3,
          title: 'Reparacin de Crdito',
          description:
            'Reconstruimos tu Crdito, mejorando y abriendo puertas a nuevas oportunidades.'
        },
        {
          avatar: service4,
          title: 'Real Estate (Bienes Races)',
          description: 'Asesora total en la compra y venta de bienes races.'
        }
      ]
    }
  },
  methods: {
    realizarLlamada() {
      window.location.href = 'tel:' + this.nroTlf;
    }
  },
  mounted() {
    this.loaded = true
  },
  computed: {
    mdUp() {
      return this.$mq === 'mdDown' || this.$mq === 'lgDown' || this.$mq === 'xl'
    }
  }
}
