import { KinesisContainer, KinesisElement } from 'vue-kinesis'
import TitleDeco from '../Title/WithDecoration'
import Hidden from '../Hidden'
import imgAPI from '~/static/images/imgAPI'
import Group1Image from '../../static/images/urpe/Group1.png'
import Group2Image from '../../static/images/urpe/Group2.png'
import Group3Image from '../../static/images/urpe/Group3.png'
import Group4Image from '../../static/images/urpe/Group4.png'


export default {
  components: {
    TitleDeco,
    Hidden,
    KinesisContainer,
    KinesisElement,
    Slick: () => import('vue-slick')
  },
  data() {
    return {
      loaded: false,
      slickOptions: {
        dots: false,
        infinite: true,
        speed: 500,
        centerMode: true,
        arrows: false,
        autoplay: true,
        autoplaySpeed: 3000,
        slidesToShow: 1,
        slidesToScroll: 1,
        imgAPI: imgAPI,
        vertical: true
      },

expertosArray: [
        Group1Image,
        Group2Image,
        Group3Image,
        Group4Image
      ],
      nroTlf: '+1 800-643-6740',

    }
  },
  methods: {
    realizarLlamada() {
      window.location.href = 'tel:' + this.nroTlf;
    }
  },
  mounted() {
    this.loaded = true
  },
  computed: {
    isDesktop() {
      const lgUp = this.$store.state.breakpoints.lgUp
      return lgUp.indexOf(this.$mq) > -1
    }
  },

}
